
  
.site-header {
  position: sticky;
  background-color: #ffffff;
  top: 0;
}
  
.site-header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* padding-top: 1rem; */
  padding-bottom: 1rem; 
}  
  
.nav__item a {
  display: block;
  padding: 1rem;
  font-size: 17px;
  font-weight: 700;
}

.nav__item,.site-header__start{
  padding: 1.3rem 0 0;
}

.nav__item a:last-child{
  padding: 0.3rem 1.2rem;
  margin-left: 10px;
  border-radius: 50px;
}

.nav__toggle {
  display: none; 
}  
    
ul{
  list-style:none;
}

.wrapper {
  max-width: 1400px;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-left: auto;
  margin-right: auto;
}

a {
  text-decoration: none;
  color: #222;
}

.button {
  background-color: #FFD500;
  border-radius: 10px;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
}

.button:hover{
  background-color: #ffee00;
  
}

.nav__wrapper {
  display: flex; 
}

@media (max-width: 960px) {
  .nav__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 99%;
    right: 0;
    left: 0;
    z-index: -1;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    background-color: #ffffff;
    padding: 20px;
    visibility: hidden;
    opacity: 0;
    transform: translateY(-100%);
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;
  }

  a>p{
    width: fit-content;
  }

  .nav__item p{
    width: fit-content;
    margin: auto;
  }

  .site-header__wrapper{
    padding-top: 0.5rem;
  }
  
  .nav__wrapper.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0); 
  } 

  .nav__toggle {
    display: block;
    position: absolute;
    right: 1.5rem;
    top: 1rem; 
    font-size: 30px;
    font-weight: 500;
  }

  .nav__item a:last-child{
    padding: 0.2rem 1.0rem;
    width: 100px;
    margin: auto;
  }
}

        