/* .App{background-color: rgb(14, 14, 14);} */
/* home */
#home{
  text-align: center;
  padding:150px 0 140px;
}
#home>p:first-child{
  font-size: 27px;
  color:#198e71;
  font-weight: 800;
  margin: 10px 0;
}
#home>h1{
  font-size: 84px;
  font-weight: 900;
  margin: 10px 0;
}
#home>p:nth-child(3){
  font-size: 18px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  letter-spacing: 5px;
  margin: 10px 0;
}
#home>p:nth-child(3)>span{
  color: gray;
  font-size: 20px;
}
#home>button{
  border: 3px solid #db9302;
  margin: 10px;
  padding: 18px 25px;
  font-size: 13px;
  font-weight: 800;
  letter-spacing: 2px;
  margin: 30px 0 0;
}
#home>button:hover{
  background-color: #db9302;
  color: #f3f8f6;
}
#home>button span{
  font-size: 15px;
}



/* about */
#about{
  padding: 100px 0 0;
  display: flex;
  justify-content: space-between;
}
#about>img{
  width: 41%;
}
#about>div{
  width: 50%;
  padding: 0 50px 0 0;
}
#about h1{
  font-size:60px;
  font-weight: 900;
  margin: 30px 0;
}
#about p{
  color: #888888;
  font-size: 24px;
  text-align: justify;
  margin: 50px 0;
}
#about h2{
  font-size: 25px;
  margin: 20px 0;
}
#about h2>span{
  font-weight: 900;
  color: #198e71;
}
#about button{
  font-size: 14px;
  letter-spacing: 3px;
  background-color: #ffbd39;
  padding: 20px 30px;
  border-radius: 60px;
  font-weight: 900;
  margin: 30px 0;
}
@media all and (max-width:1300px){
  #about h1{
    text-align: center;
  }
  #about{
    flex-direction: column-reverse;
  }
  #about>img{
    width: 80%;
    margin:0 auto;
  }
  #about>div{
    width: 90%;
    margin:auto;
    padding: 0;
  }
}


/* skills */
#skills{
  padding: 120px 20px 0;
  text-align: center;
  /* height: 1000px; */
}
#skills h1{
  font-size:60px;
  font-weight: 900;
}

#skills h3{
  font-size: 20px;
  font-weight: 700;
  color: #198e71;
}
#skills>div>div{
  display: grid;
  grid-template-columns: repeat(5,1fr);
  row-gap: 50px;
  margin:50px 20px 0;
  justify-content: space-between;
  align-items: center;
}
.skills-card{
  display: flex;
  padding: auto;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  width: 100px;
  height: 150px;
  cursor: -webkit-grab;
  margin: auto;  transition: transform 2s;
}
.skills-card:hover{
  transform: rotate(360deg);
}
#skills p{
  color: #888888;
  font-size: 24px;
  margin: 20px 0;
}
@media all and (max-width:1350px){
  #skills>div>div{
    grid-template-columns: repeat(4,1fr);
  }
}

@media all and (max-width:750px){
  #skills>div>div{
    grid-template-columns: repeat(3,1fr);
    /* column-gap: 170px; */
  }
}
@media all and (max-width:590px){
  #skills>div>div{
    grid-template-columns: repeat(2,1fr);
    
    column-gap: 50px;
  }
}



/* projects */
#projects{
  padding: 100px 0px 20px;
}
#projects>div{
  /* border: 5px solid #2df8c5; */
  display:flex;
  margin: 15px 5px;
}
#projects h1{
  font-size:60px;
  font-weight: 900;
  margin-bottom: 50px;
  text-align: center;
}
#projects img{
  /* border: 5px solid #2df8c5; */
  /* width: 97%; */
  /* border: 1px solid #07634c; */
  height: 100%;
  /* margin: 5px; */
  /* align-items:center; */
  /* margin:auto auto; */
  transition: 0.4s all ease-in-out;
}
/* img{
  width:50%;
  align-items:center;
  margin:auto auto;
  transition: 0.4s all ease-in-out;
} */


#projects>div>div:hover img{
cursor: -webkit-zoom-in;
transform: scale(1.3);
}
#projects>div>div{
  /* padding: 5px; */
  border: 5px solid #07634c ;
  overflow: hidden;
  /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; */
  /* border: 3px solid #198e71; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  margin: 0.4px;
  background-color: #07634c;
  /* background-color: #198e71; */
}
#projects>div:nth-child(2)>div:last-child,#projects>div:nth-child(3)>div:first-child,#projects>div:nth-child(4)>div:last-child,#projects>div:nth-child(5)>div:first-child{
  padding: 30px;
}
.project-title{
  font-size: 32px;
  font-weight: 700;
  color: #2df8c5;
}
.project-description{
  color: rgb(196, 227, 204);
  margin: 20px 0;
  font-size: 24px;
  text-align: justify;
}
.project-tech-stack{
  /* width: fit-content; */
  display: grid;
  grid-template-columns: repeat(5,1fr);
  gap: 20px;
  margin: 20px 0;
  justify-content: center;
}
.project-tech-stack>div{
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  /* color: #667507eb; */
  color: #0f5745;
  background-color: #2df8c5;
  /* background-color: #e3f280eb; */
  padding: 5px 10px;
}
.project-deployed-link{
  /* background-color: #667507eb; */
  border: 3px solid rgb(196, 227, 204);
  color: rgb(196, 227, 204);
  font-weight: 500;
  padding: 8px 20px;
  border-radius: 50px;
  width: fit-content;
}
.project-deployed-link:hover{
  color: #07634c;
  background-color: rgb(196, 227, 204);
}
.project-github-link{
  color: rgb(196, 227, 204);
}
.project-github-link:hover{
  color: #9ce6d3;
}
.project-tech-stack~div{
  display: flex;
  margin: 30px 0 0;
  gap: 30px;
  align-items: center;
  justify-content: flex-start;
}
@media all and (max-width:1530px) {
  #projects>div:nth-child(2),#projects>div:nth-child(4){
    flex-direction: column;
    margin-bottom: 20px;
  }
  #projects>div:nth-child(3){
    flex-direction: column-reverse;
    margin-bottom: 20px;
  }
  #projects>div>div{
    width: 95%;
    margin: auto;
    box-shadow: none;
  }
  /* #projects img{
    margin: 10px;
    border: 2px solid #559648;
  } */
}
@media all and (max-width:770px) {
  .project-tech-stack{
    grid-template-columns: repeat(3,1fr);
    
  }
}
@media all and (max-width:530px) {
  .project-tech-stack{
    grid-template-columns: repeat(2,1fr);
  }
  #home>h1{
    font-size: 50px;
  }
  #projects>h1,#skills>h1,#about h1{
    font-size: 40px;
  }
  #skills p,#projects p,#about p{
    font-size: 20px;
  }
  #home>p:nth-child(3){
    letter-spacing: 2px;
  }
  .project-title{
    font-size: 25px;
  }
}




/* github */
#github{
  padding: 100px 0 20px;
}
#github h1{
  font-size:60px;
  font-weight: 900;
  margin-bottom: 50px;
  text-align: center;
}
#stat{
  margin-top: 120px;
}
#github>div:last-child{
  display: flex;
  margin: auto;
  width: 99.5%;
}
#github>div:last-child img{
  height:215px;
}
#github>div:nth-child(2){
  border: 1.5px solid #e3f280eb;
  background-color: #e3f280eb;
  margin: 50px 5px;
  padding: 0 5px;
}
@media all and (max-width:530px) {
  #github h1{
    font-size: 40px;
  }
}
@media all and (max-width:1300px){
  #github>div:last-child{
    flex-direction: column;
  }
  #github>div:last-child img{
    width: 99.5%;
    margin: auto;
    height: fit-content;
  }
}

/* contact */
#contact{
  padding: 100px 30px;
  text-align: center;
}
#contact h1{
  font-size:60px;
  font-weight: 900;
  margin-bottom: 50px;
  text-align: center;
}
#contact p{
  font-size: 24px;
  margin:20px 0 ;
  font-weight: 700;
  /* color: #198e71; */
}

#contact p:last-child{
  font-size: 20px;
  font-weight: 500;
}
#contact>div{
  display: grid;
  padding: 60px 0;
  grid-template-columns: repeat(4,1fr);
}
#contact>div>*{
  margin: auto;
}

#contact>div>div div{
  font-size: 50px;
  width: fit-content;
  color: #db9302;
  margin: auto;
  background-color: #ffffff1a;
  padding: 40px;
  border-radius: 50%;
  border: 1.5px solid #db9302;
}
#contact-github:hover,#contact-linkedin:hover{
  cursor: pointer;
  color: #db9302;
}
@media all and (max-width:1000px){
  #contact>div{
    grid-template-columns: repeat(2,1fr);
    row-gap: 50px;
  }
}
@media all and (max-width:570px){
  #contact>div{
    grid-template-columns: repeat(1,1fr);
    row-gap: 20px;
  }
  
  #contact>div>div div{
    font-size: 30px;
    padding: 25px;
  }
}
@media all and (max-width:530px){
  #contact h1{
    font-size: 40px;
    margin-bottom:0 ;
  }
}



/* scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background:#e3f280eb;
}

::-webkit-scrollbar-thumb {
  background: #f5dc62;
  border-radius: 5px;
}

